p,
h2 {
  margin: 0;
  padding: 1rem;
}

.grid-container {
  display: grid;
  height: 100%;
  /* max-width: ; */
  grid-template-columns: 1.6fr 0.4fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 1px 1px;
  grid-template-areas: "text web" "description repo";
  /* margin: 300px; */
  padding: 2rem;

  background: #e2e6e8;
  color: #16191b;
}

.text {
  grid-area: text;
  font-size: 2.8rem;
  /* border: 3px solid purple; */
  width: auto;
}

.description {
  grid-area: description;
  /* border: 3px solid green; */
  font-size: 1.3rem;
}

.web {
  grid-area: web;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  /* border: 3px solid red; */
  width: auto;
  height: auto;
}

.repo {
  grid-area: repo;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  /* border: 3px solid blue; */
}

.logo {
  width: 50px;
  height: 50px;
}



